//    
export const signUp = createAsyncThunk(
  'auth/signUp',
  async (userData, { rejectWithValue }) => {
    try {
      const response = await signUpAPI(userData);
      return { success: true, ...response };
    } catch (error) {
      return { success: false, ...error };
    }
  }
);

